.CardTestimonial {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    text-align: left;
}

.CardTestimonial img {
    width: 45px;
    position: absolute;
}

.CardTestimonial p {
    text-align: left;
    color: white;
    font-size: 1em;
    margin-top: 40px;
    line-height: 157.2%;
    font-size: 1.1em;
    font-style: italic;
    text-align: justify;
    margin-right: 10px;
}

.CardTestimonial h4 {
    color: white;
    font-weight: 800;
    font-size: 1.5em;
}

.CardTestimonial h6 {
    color: white;
    font-weight: 300;
    font-size: 1.2em;
    margin-top: 0;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}